"use client";

import { useState, FormEvent } from "react";

interface FinanceEntryFormProps {
  onAddEntry: (entry: FinanceEntry) => void;
}

export interface FinanceEntry {
  type: "income" | "expense";
  amount: number;
  description: string;
  date: string;
}

export default function FinanceEntryForm({ onAddEntry }: FinanceEntryFormProps) {
  const [type, setType] = useState<"income" | "expense">("income");
  const [amount, setAmount] = useState<string>("");
  const [description, setDescription] = useState<string>("");
  const [date, setDate] = useState<string>(() => {
    const today = new Date();
    return today.toISOString().split("T")[0];
  });

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    const amountNumber = Number(amount);
    if (!amount || isNaN(amountNumber) || amountNumber <= 0) {
      alert("Masukkan jumlah uang yang valid.");
      return;
    }
    if (!description.trim()) {
      alert("Masukkan keterangan.");
      return;
    }
    const entry: FinanceEntry = {
      type,
      amount: amountNumber,
      description: description.trim(),
      date,
    };
    onAddEntry(entry);
    setAmount("");
    setDescription("");
    setDate(new Date().toISOString().split("T")[0]);
  };

  return (
    <form onSubmit={handleSubmit} className="mb-8 p-4 border border-gray-300 rounded-md bg-gray-50">
      <h2 className="text-xl font-semibold mb-4">Tambah Transaksi Baru</h2>
      <div className="flex flex-col sm:flex-row sm:space-x-4 gap-4">
        <div className="flex flex-col flex-1">
          <label htmlFor="type" className="mb-1 font-medium">
            Jenis
          </label>
          <select
            id="type"
            value={type}
            onChange={(e) => setType(e.target.value as "income" | "expense")}
            className="border border-gray-300 rounded px-3 py-2"
          >
            <option value="income">Pemasukan</option>
            <option value="expense">Pengeluaran</option>
          </select>
        </div>
        <div className="flex flex-col flex-1">
          <label htmlFor="amount" className="mb-1 font-medium">
            Jumlah (Rp)
          </label>
          <input
            id="amount"
            type="number"
            min="0"
            step="1000"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            className="border border-gray-300 rounded px-3 py-2"
            placeholder="Masukkan jumlah uang"
          />
        </div>
        <div className="flex flex-col flex-1">
          <label htmlFor="date" className="mb-1 font-medium">
            Tanggal
          </label>
          <input
            id="date"
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            className="border border-gray-300 rounded px-3 py-2"
          />
        </div>
      </div>
      <div className="flex flex-col mt-4">
        <label htmlFor="description" className="mb-1 font-medium">
          Keterangan
        </label>
        <textarea
          id="description"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          className="border border-gray-300 rounded px-3 py-2 resize-none"
          rows={3}
          placeholder="Masukkan keterangan transaksi"
        />
      </div>
      <button
        type="submit"
        className="mt-4 bg-black text-white py-2 px-4 rounded hover:bg-gray-800 transition-colors"
      >
        Tambah
      </button>
    </form>
  );
}
