"use client";

import { useState, useEffect } from "react";
import FinanceEntryForm, { FinanceEntry } from "../../components/FinanceEntryForm";
import FinanceSummary from "../../components/FinanceSummary";

export default function FinancePage() {
  const [entries, setEntries] = useState<FinanceEntry[]>(() => {
    if (typeof window !== "undefined") {
      const saved = localStorage.getItem("financeEntries");
      return saved ? JSON.parse(saved) : [];
    }
    return [];
  });

  useEffect(() => {
    if (typeof window !== "undefined") {
      localStorage.setItem("financeEntries", JSON.stringify(entries));
    }
  }, [entries]);

  const addEntry = (entry: FinanceEntry) => {
    setEntries((prev: FinanceEntry[]) => [...prev, entry]);
  };

  return (
    <main className="min-h-screen bg-gradient-to-r from-indigo-100 via-purple-100 to-pink-100 text-black p-6 font-sans max-w-4xl mx-auto rounded-lg shadow-lg">
      <header className="mb-8 flex flex-col items-center">
        <h1 className="text-4xl font-extrabold mb-2 text-indigo-900 drop-shadow-md">
          Aplikasi Pengatur Pemasukan dan Pengeluaran
        </h1>
      </header>
      <FinanceEntryForm onAddEntry={addEntry} />
      <FinanceSummary entries={entries} />
      <section className="mt-8 bg-white bg-opacity-90 rounded-md p-6 shadow-lg border border-indigo-300">
        <h2 className="text-2xl font-semibold mb-4 text-indigo-900">Daftar Transaksi</h2>
        {entries.length === 0 ? (
          <p className="text-indigo-700">Tidak ada transaksi.</p>
        ) : (
          <table className="w-full border border-indigo-300 rounded-md">
            <thead>
              <tr className="bg-indigo-300 text-indigo-900">
                <th className="border border-indigo-400 p-3 text-left">Tanggal</th>
                <th className="border border-indigo-400 p-3 text-left">Jenis</th>
                <th className="border border-indigo-400 p-3 text-right">Jumlah (Rp)</th>
                <th className="border border-indigo-400 p-3 text-left">Keterangan</th>
              </tr>
            </thead>
            <tbody>
              {entries
                .slice()
                .sort((a: FinanceEntry, b: FinanceEntry) => new Date(b.date).getTime() - new Date(a.date).getTime())
                .map((entry: FinanceEntry, idx: number) => (
                  <tr key={idx} className="odd:bg-white even:bg-indigo-100">
                    <td className="border border-indigo-400 p-3">{new Date(entry.date).toLocaleDateString("id-ID")}</td>
                    <td className="border border-indigo-400 p-3">{entry.type === "income" ? "Pemasukan" : "Pengeluaran"}</td>
                    <td className="border border-indigo-400 p-3 text-right">{entry.amount.toLocaleString("id-ID")}</td>
                    <td className="border border-indigo-400 p-3">{entry.description}</td>
                  </tr>
                ))}
            </tbody>
          </table>
        )}
      </section>
    </main>
  );
}
