"use client";

import { FinanceEntry } from "./FinanceEntryForm";
import { useState } from "react";

interface FinanceSummaryProps {
  entries: FinanceEntry[];
}

interface Summary {
  totalIncome: number;
  totalExpense: number;
  descriptions: { [key: string]: number };
}

function getWeekNumber(date: Date) {
  const firstDayOfYear = new Date(date.getFullYear(), 0, 1);
  const pastDaysOfYear = (date.getTime() - firstDayOfYear.getTime()) / 86400000;
  return Math.ceil((pastDaysOfYear + firstDayOfYear.getDay() + 1) / 7);
}

function summarizeEntries(entries: FinanceEntry[], filterFn: (date: Date) => boolean): Summary {
  const summary: Summary = {
    totalIncome: 0,
    totalExpense: 0,
    descriptions: {},
  };

  entries.forEach((entry) => {
    const entryDate = new Date(entry.date);
    if (filterFn(entryDate)) {
      if (entry.type === "income") {
        summary.totalIncome += entry.amount;
      } else {
        summary.totalExpense += entry.amount;
      }
      if (entry.description in summary.descriptions) {
        summary.descriptions[entry.description] += entry.amount;
      } else {
        summary.descriptions[entry.description] = entry.amount;
      }
    }
  });

  return summary;
}

export default function FinanceSummary({ entries }: FinanceSummaryProps) {
  const [filter, setFilter] = useState<"week" | "month" | "year">("week");

  const now = new Date();

  const filteredSummary = summarizeEntries(entries, (date) => {
    if (filter === "week") {
      return (
        date.getFullYear() === now.getFullYear() &&
        getWeekNumber(date) === getWeekNumber(now)
      );
    } else if (filter === "month") {
      return date.getFullYear() === now.getFullYear() && date.getMonth() === now.getMonth();
    } else {
      return date.getFullYear() === now.getFullYear();
    }
  });

  return (
    <section className="mt-8 p-4 border border-gray-300 rounded-md bg-gray-50">
      <h2 className="text-2xl font-semibold mb-4">Ringkasan {filter === "week" ? "Mingguan" : filter === "month" ? "Bulanan" : "Tahunan"}</h2>
      <div className="mb-4">
        <label htmlFor="filter" className="mr-2 font-medium">
          Pilih Periode:
        </label>
        <select
          id="filter"
          value={filter}
          onChange={(e) => setFilter(e.target.value as "week" | "month" | "year")}
          className="border border-gray-300 rounded px-3 py-2"
        >
          <option value="week">Minggu Ini</option>
          <option value="month">Bulan Ini</option>
          <option value="year">Tahun Ini</option>
        </select>
      </div>
      <div className="mb-4">
        <p>
          <strong>Total Pemasukan:</strong> Rp {filteredSummary.totalIncome.toLocaleString("id-ID")}
        </p>
        <p>
          <strong>Total Pengeluaran:</strong> Rp {filteredSummary.totalExpense.toLocaleString("id-ID")}
        </p>
      </div>
      <div>
        <h3 className="font-semibold mb-2">Rincian Keterangan:</h3>
        {Object.keys(filteredSummary.descriptions).length === 0 ? (
          <p>Tidak ada data.</p>
        ) : (
          <ul className="list-disc list-inside max-h-48 overflow-auto">
            {Object.entries(filteredSummary.descriptions).map(([desc, amount]) => (
              <li key={desc}>
                {desc}: Rp {amount.toLocaleString("id-ID")}
              </li>
            ))}
          </ul>
        )}
      </div>
    </section>
  );
}
